.main-page {
	position: relative;
	background: linear-gradient(-4deg, #8cbb06 23.2%, #ffffff 96.56%);
	width: 100%;
	padding: rem(75px) rem(80px) rem(10px);
	@media (max-width: 1024px) {
		padding: rem(15px) rem(10px) rem(10px);
	}
	&__oblaka {
		position: absolute !important;
		width: 90%;
		div {
			display: flex !important;
			justify-content: space-between;
		}
	}
	&__grass-01 {
		position: absolute;
		width: 110%;
		// bottom: -107%;
		top: rem(480px);
		z-index: 5;
		left: 50%;
		transform: translateX(-50%);
		min-width: rem(1440px);
		@media (max-width: 1024px) {
			min-width: rem(1024px);
		}
		@media (max-width: 768px) {
			min-width: rem(768px);
			top: rem(436px);
		}
		@media (max-width: 480px) {
			min-width: rem(768px);
			top: rem(416px);
		}
		@media (max-width: 375px) {
			min-width: 375px;
			top: rem(396px);
		}
		img {
			width: 100%;
		}
	}
	&__grass-02 {
		position: absolute;
		width: 110%;
		bottom: 0%;
		z-index: 3;
		left: 50%;
		transform: translateX(-50%);
		min-width: rem(1440px);
		@media (max-width: 1024px) {
			min-width: rem(1024px);
			bottom: 5%;
		}
		@media (max-width: 768px) {
			min-width: rem(768px);
			bottom: 13%;
		}
		img {
			width: 100%;
		}
		@media (max-width: 375px) {
			min-width: 375px;
			bottom: 20%;
		}
	}
	&__circle-01 {
		position: absolute;
		width: 85%;
		min-width: rem(1340px);
		bottom: -49%;
		left: 0;
		z-index: 2;
		img {
			width: 100%;
		}
		@media (max-width: 1024px) {
			min-width: 1024px;
			bottom: -30%;
		}
		@media (max-width: 768px) {
			left: -15%;
			min-width: 768px;
			bottom: -5%;
		}
		@media (max-width: 480px) {
			min-width: 580px;
			bottom: 13%;
		}
		@media (max-width: 375px) {
			min-width: 500px;
			left: -20%;
			bottom: 14%;
		}
	}
	&__circle-02 {
		position: absolute;
		width: 65%;
		min-width: rem(1080px);
		bottom: -43%;
		right: 0;
		z-index: 1;
		@media (max-width: 1024px) {
			display: none;
		}
		img {
			width: 100%;
		}
	}
	&__cat {
		position: absolute;
		width: rem(346px);
		left: 65%;
		bottom: 10%;
		z-index: 3;
		transform: translateX(rem(900px));
		transition: 1.5s transform 0.8s;
		img {
			width: 100%;
		}
		&._active {
			transform: translateX(0%);
		}
		@media (max-width: 1280px) {
			width: rem(306px);
		}
		@media (max-width: 1024px) {
			width: rem(276px);
			left: 70%;
			bottom: 12%;
		}
		@media (max-width: 768px) {
			left: 55%;
		}
		@media (max-width: 480px) {
			width: rem(188px);
			left: 60%;
			bottom: 20%;
		}
		@media (max-width: 375px) {
			bottom: 15%;
			left: 56%;
		}
	}
	&__tea {
		position: absolute;
		z-index: 4;
		width: rem(268px);
		left: 59.5%;
		bottom: 15%;
		transform: translateX(rem(900px));
		transition: 1.5s transform 0.5s;
		&._active {
			transform: translateX(0%);
		}
		@media (max-width: 1280px) {
			width: rem(246px);
		}
		@media (max-width: 1024px) {
			width: rem(206px);
			bottom: 15%;
			left: 67%;
		}
		@media (max-width: 768px) {
			left: 52%;
		}
		@media (max-width: 480px) {
			width: rem(116px);
			bottom: 20%;
			left: 57%;
		}
		@media (max-width: 375px) {
			left: 52%;
		}
		&-el {
			position: absolute;
		}
		img {
			width: 100%;
		}
	}
	&__tree-01 {
		position: absolute;
		min-width: rem(149px);
		left: 2%;
		bottom: 35%;
		@media (max-width: 1024px) {
			display: none;
		}
		img {
			width: 100%;
		}
	}
	&__tree-02 {
		position: absolute;
		min-width: rem(149px);
		left: 53%;
		bottom: 37%;
		img {
			width: 100%;
		}
		@media (max-width: 1024px) {
			min-width: rem(109px);
			max-width: rem(109px);
			left: 63%;
			bottom: 45%;
		}
		@media (max-width: 768px) {
			min-width: rem(85px);
			max-width: rem(85px);
			left: 40%;
			bottom: 59%;
		}
		@media (max-width: 480px) {
			min-width: rem(65px);
			max-width: rem(65px);
			left: 65%;
			bottom: 58%;
		}
		@media (max-width: 375px) {
			left: 68%;
			bottom: 60%;
		}
	}
	&__tree-03 {
		position: absolute;
		min-width: rem(118px);
		right: 0.5%;
		bottom: 41%;
		@media (max-width: 1024px) {
			display: none;
		}
		img {
			width: 100%;
		}
	}
	&__content {
		position: relative;
		z-index: 50;
		max-width: rem(1080px);
		margin: 0 auto;
		width: 100%;
		opacity: 0;
		transform: translate(0, -20%);
		transition: 1s opacity, 1s transform;
		&._active {
			opacity: 1;
			transform: translate(0, 0%);
		}
	}
	&__logo {
		position: relative;
		width: rem(276px);
		height: rem(94.22px);
		margin-bottom: rem(27px);
		img {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			object-fit: contain;
		}
		@media (max-width: 1024px) {
			margin: 0 auto 12px;
		}
		@media (max-width: 480px) {
			width: rem(192px);
			height: rem(65.52px);
		}
	}
	&__title {
		font-weight: bold;
		font-size: rem(60px);
		line-height: rem(72px);
		margin-bottom: rem(181px);
		@media (max-width: 1024px) {
			text-align: center;
		}
		@media (max-width: 768px) {
			font-weight: bold;
			font-size: 26px;
			line-height: 31px;
			margin-bottom: rem(95px);
		}
		@media (max-width: 375px) {
			font-size: 24px;
		}
	}
	&__labels {
	}
}
.labels-main {
	display: inline-flex;
	align-items: flex-start;
	margin-bottom: rem(158px);
	@media (max-width: 768px) {
		flex-direction: column;
		justify-content: center;
		align-items: flex-start;
		margin-bottom: rem(117px);
	}
	@media (max-width: 375px) {
		margin-bottom: rem(67px);
	}
	&__item {
		display: flex;
		align-items: flex-start;
		margin-right: rem(33px);
		&:last-child {
			margin-right: 0px;
		}
		@media (max-width: 768px) {
			margin-right: 0;
			margin-bottom: 8px;
			&:last-child {
				margin-bottom: 0px;
			}
		}
	}
	&__text {
		font-size: rem(20px);
		line-height: rem(24px);
	}
	&__icon {
		position: relative;
		width: rem(53px);
		height: rem(43px);
		margin-right: rem(8px);
		img {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			object-fit: contain;
			z-index: 3;
		}
		span {
			font-weight: bold;
			font-size: rem(12px);
			line-height: rem(14px);
			position: absolute;
			z-index: 4;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			user-select: none;
			svg {
				width: rem(18px);
				height: rem(14px);
				transform: translateY(rem(3px));
			}
		}
		@media (max-width: 768px) {
			width: rem(63px);
			height: rem(53px);
		}
	}
}

.warranty {
	bottom: rem(10px);
	&__title {
		font-weight: bold;
		font-size: rem(40px);
		line-height: rem(48px);
		color: #ffffff;
		margin: 0 0 rem(10px);
		@media (max-width: 768px) {
			font-size: 21px;
			line-height: 25px;
		}
		@media (max-width: 375px) {
			font-size: 20px;
		}
	}
	&__logo {
		display: inline-flex;
		align-items: flex-end;
		&-container {
			position: relative;
			width: rem(107px);
			height: rem(45.05px);
			margin-right: rem(4px);
			img {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				object-fit: contain;
			}
			@media (max-width: 375px) {
				width: rem(80px);
				height: rem(33.05px);
			}
		}
		> span {
			font-size: rem(20px);
			line-height: rem(34px);
			color: #ffffff;
			@media (max-width: 768px) {
				font-size: 14px;
			}
		}
	}
}
