.all-products {
	text-align: center;
	margin-bottom: rem(115px);
	position: relative;
	padding: rem(112px) 0 0 0;
	z-index: 10;
	@media (max-width: 479.98px) {
		padding: 60px 0px 0px 0px;
	}
	@media (max-width: 399px) {
		margin: 0px 0px 40px 0px;
	}
	&__title {
		font-weight: bold;
		font-size: rem(60px);
		line-height: rem(72px);
		position: relative;
		z-index: 50;
		padding: 0 10px;
		opacity: 0;
		transform: translateY(-30px);
		transition: 1s opacity 0.3s, 1s transform 0.3s;
		@media (max-width: 767.98px) {
			font-size: 50px;
			line-height: 120%;
			margin: 0px 0px 11px 0px;
		}
		@media (max-width: 479.98px) {
			font-size: 35px;
		}
		@media (max-width: 399px) {
			font-size: 22px;
		}
		&._active {
			opacity: 1;
			transform: translateY(0%);
		}
	}
	&__logo {
		margin: 0 auto;
		width: rem(246px);
		height: rem(104px);
		position: relative;
		margin-bottom: rem(38px);
		position: relative;
		z-index: 50;
		opacity: 0;
		transform: translateY(-30px);
		transition: 1s opacity 0.3s, 1s transform 0.3s;
		@media (max-width: 767.98px) {
			margin-bottom: 11px;
		}
		@media (max-width: 399px) {
			width: 184px;
		}
		img {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			object-fit: contain;
		}
		&._active {
			opacity: 1;
			transform: translateY(0%);
		}
	}
	&__text {
		font-size: rem(24px);
		line-height: rem(29px);
		max-width: rem(446px);
		margin: 0 auto rem(39px);
		position: relative;
		padding: 0px 10px;
		z-index: 50;
		opacity: 0;
		transform: translateY(-30px);
		transition: 1s opacity 0.3s, 1s transform 0.3s;
		@media (max-width: 767.98px) {
			font-size: 20px;
		}
		@media (max-width: 399px) {
			max-width: 272px;
			font-size: 16px;
			text-align: left;
			line-height: 19px;
			margin: 0px 0px 20px 0px;
		}
		&._active {
			opacity: 1;
			transform: translateY(0%);
		}
	}
	&__button {
		position: relative;
		z-index: 50;
		display: flex;
		align-items: center;
		justify-content: center;
		margin: 0 auto;
		background: #8cbb06;
		box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2), inset 0px -8px 8px #769e03, inset 0px 8px 8px #aae10c;
		border-radius: rem(36px);
		height: rem(71px);
		max-width: rem(217px);
		width: 100%;
		opacity: 0;
		transform: translateY(-30px);
		transition: 1s opacity 0.3s, 1s transform 0.3s, 0.3s background, 0.3s box-shadow;
		span {
			font-weight: bold;
			font-size: rem(24px);
			line-height: rem(29px);
			color: #ffffff;
		}
		@media (min-width: 1024px) {
			&:hover {
				background: #a1d116;
				box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2), inset 0px -8px 8px #769e03, inset 0px 8px 8px #aae10c;
				transition: 0.3s background, 0.3s box-shadow;
			}
		}

		@media (max-width: 767.98px) {
			height: 50px;
		}
		@media (max-width: 399px) {
			max-width: 185px;
			height: 39px;
			span {
				font-size: 16px;
			}
		}
		&._active {
			opacity: 1;
			transform: translateY(0%);
		}
	}
	&__circle-01 {
		position: absolute;
		left: 0%;
		top: 10%;
		z-index: 9;
		width: 100%;
		img {
			width: 100%;
		}
		@media (max-width: 479.98px) {
			top: 0;
			left: -25%;
			height: 400px;
		}
	}
	&__circle-02 {
		position: absolute;
		right: 0;
		top: 0%;
		z-index: 8;
		width: 80%;
		img {
			width: 100%;
		}
		@media (max-width: 479.98px) {
		}
	}
	&__tree-01 {
		position: absolute;
		top: 114%;
		left: 42%;
		width: rem(150px);
		z-index: 10;
		img {
			width: 100%;
		}
		@media (max-width: 767.98px) {
			display: none;
		}
	}
	&__tree-02 {
		position: absolute;
		top: 92%;
		left: 65%;
		width: rem(137px);
		z-index: 10;
		img {
			width: 100%;
		}
		@media (max-width: 767.98px) {
			width: 100px;
			top: 100%;
			left: 15%;
		}
		@media (max-width: 479.98px) {
			left: 5%;
			width: 85px;
			top: 102%;
		}
		@media (max-width: 399px) {
			width: 56px;
			top: 95%;
		}
	}
	&__tree-03 {
		position: absolute;
		top: 118%;
		left: 80%;
		width: rem(86px);
		z-index: 10;
		img {
			width: 100%;
		}
		@media (max-width: 767.98px) {
			width: 60px;
			left: 40%;
		}
		@media (max-width: 479.98px) {
			left: 25%;
		}
		@media (max-width: 399px) {
			width: 33px;
			top: 106%;
			left: 25%;
		}
	}
}
