.goods {
	width: 100%;
	padding: rem(116px) 0 0 0;
	position: relative;
	@media (max-width: 1024px) {
		padding: rem(76px) 0 0 0;
	}
	@media (max-width: 768px) {
		padding: rem(46px) 0 0 0;
	}
	&__oblako-01 {
		position: absolute;
		width: rem(160px);
		left: 0;
		top: 15%;
		z-index: 6;
		img {
			width: 100%;
		}
		@media (max-width: 1024px) {
			display: none;
		}
	}
	&__oblako-02 {
		position: absolute;
		width: rem(160px);
		right: 0;
		top: 32%;
		z-index: 6;
		img {
			width: 100%;
		}
		@media (max-width: 1024px) {
			display: none;
		}
	}
	&__circle-01 {
		position: absolute;
		width: 100%;
		left: 0;
		top: rem(40px);
		z-index: 6;
		img {
			width: 100%;
		}
	}
	&__circle-02 {
		position: absolute;
		width: 100%;
		left: 0;
		top: 0;
		z-index: 5;
		img {
			width: 100%;
		}
	}
	&__circle-02 {
		position: absolute;
		width: 100%;
		right: 0;
		top: 0;
		img {
			width: 100%;
		}
	}
	&__title {
		position: relative;
		z-index: 50;
		font-weight: bold;
		font-size: rem(60px);
		line-height: rem(72px);
		margin-bottom: rem(66px);
		text-align: center;
		opacity: 0;
		transform: translateY(-10%);
		transition: 1s opacity 0.3s, 1s transform 0.3s;
		@media (max-width: 1024px) {
			font-size: 34px;
			line-height: 100%;
			margin-bottom: rem(56px);
		}
		@media (max-width: 768px) {
			font-size: 22px;
			line-height: 100%;
			margin-bottom: rem(35px);
		}
		&._active {
			opacity: 1;
			transform: translateY(0%);
		}
	}
	&__row {
		position: relative;
		z-index: 50;
		width: 100%;
		min-height: rem(982px);
		opacity: 0;
		transform: translateY(-10%);
		transition: 1s opacity 0.3s, 1s transform 0.3s;
		&._active {
			opacity: 1;
			transform: translateY(0%);
		}
		// @media (max-width: 1280px) {
		// 	min-height: rem(882px);
		// }
		// @media (max-width: 1180px) {
		// 	min-height: rem(782px);
		// }
		// @media (max-width: 1080px) {
		// 	min-height: rem(682px);
		// }
		// @media (max-width: 840px) {
		// 	min-height: rem(582px);
		// }
		// @media (max-width: 768px) {
		// 	min-height: rem(482px);
		// }
		@media (max-width: 1024px) {
			min-height: rem(620px);
		}
		@media (max-width: 680px) {
			display: flex;
			flex-wrap: wrap;
			min-height: auto;
		}
	}
	&__item {
		position: absolute;
		overflow: hidden;
		width: 49.5%;
		height: 49%;
		box-shadow: 0px 0px 20px rgba(10, 36, 60, 0.25);
		border-radius: 10px;
		z-index: 3;
		transition: 0.8s height, 2s z-index;
		.goods-item {
			&::-webkit-scrollbar {
				width: rem(4px); /* ширина для вертикального скролла */
				height: rem(4px); /* высота для горизонтального скролла */
				background-color: transparent;
			}
			&::-webkit-scrollbar-thumb {
				background-color: #ffffff;
				border-radius: 9em;
			}
		}
		&--top-left {
			top: 0;
			left: 0;
			background: #73be42;
		}
		&--top-right {
			top: 0;
			right: 0;
			background: #bb066e;
		}
		&--bottom-left {
			bottom: 0;
			left: 0;
			background: #18ade3;
		}
		&--bottom-right {
			bottom: 0;
			right: 0;
			background: #784c9f;
		}
		@media (max-width: 680px) {
			position: relative;
			display: block;
			width: 100%;
			height: auto;
			min-height: 230px;
			margin-bottom: 8px;
			max-height: 230px;
		}
		@media (max-width: 480px) {
			min-height: 190px;
			max-height: 190px;
		}
		@media (max-width: 375px) {
			min-height: 130px;
			max-height: 130px;
		}
	}
}

.item-goods {
	&__animal {
		position: absolute;
		bottom: -3px;
		left: 0;
		width: 100%;
		padding: 0 0 62.5% 0;
		@media (max-width: 680px) {
			display: none;
		}
		img {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
		&--mobile {
			display: none;
			@media (max-width: 680px) {
				display: block;
				padding: 0 0 30%;
			}
		}
	}
	&__content {
		padding: rem(36px) rem(40px) rem(80px);
		width: 100%;
		height: 100%;
		position: relative;
		z-index: 5;
		display: flex;
		flex-direction: column;
		@media (max-width: 1180px) {
			padding: rem(24px) rem(30px) rem(80px);
		}
		@media (max-width: 840px) {
			padding: rem(12px) rem(16px) rem(80px);
		}
		@media (max-width: 768px) {
			padding: rem(12px) rem(16px) rem(70px);
		}
		@media (max-width: 480px) {
			padding: rem(12px) rem(16px) rem(50px);
		}
		@media (max-width: 420px) {
			padding: rem(12px) rem(16px) rem(40px);
		}
		@media (max-width: 375px) {
			padding: rem(12px) rem(16px) rem(30px);
		}
		&-grass {
			position: absolute;
			z-index: 50;
			width: 100%;
			bottom: 0;
			left: 50%;
			opacity: 0;
			visibility: hidden;
			transform: translateX(-50%);
			img {
				width: 100%;
			}
		}
		&--green {
			&:after {
				content: "";
				z-index: 1;
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				opacity: 0;
				visibility: hidden;
				transition: 0.8s opacity, 0.8s visibility;
				background: linear-gradient(180deg, #ffffff -6.64%, #73be42 100%);
			}
		}
		&--violet {
			&:after {
				content: "";
				z-index: 1;
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				opacity: 0;
				visibility: hidden;
				transition: 0.8s opacity, 0.8s visibility;
				background: linear-gradient(180deg, #ffffff -6.64%, #784c9f 100%);
			}
		}
		&--blue {
			&:after {
				content: "";
				z-index: 1;
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				opacity: 0;
				visibility: hidden;
				transition: 0.8s opacity, 0.8s visibility;
				background: linear-gradient(180deg, #ffffff -6.64%, #18ade3 100%);
			}
		}
		&--pink {
			&:after {
				content: "";
				z-index: 1;
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				opacity: 0;
				visibility: hidden;
				transition: 0.8s opacity, 0.8s visibility;
				background: linear-gradient(180deg, #ffffff -6.64%, #bb066e 100%);
			}
		}
	}
	&__body {
		opacity: 0;
		visibility: hidden;
		padding: rem(20px) 0 0 0;
		transform: translateY(-40px);
	}
	&__title {
		display: flex;
		align-items: center;
		justify-content: space-between;
		cursor: pointer;
		position: relative;
		z-index: 4;
		span {
			font-weight: bold;
			font-size: rem(40px);
			line-height: rem(48px);
			color: #ffffff;
			transition: 0.5s color;
			@media (max-width: 1180px) {
				font-size: rem(32px);
			}
			@media (max-width: 1080px) {
				font-size: rem(24px);
			}
			@media (max-width: 480px) {
				font-size: 20px;
			}
		}
		svg {
			transition: 0.3s transform;
			width: rem(24px);
			height: rem(24px);
		}
		path {
			transition: 0.5s fill;
		}
		@media (min-width: 1025px) {
			&:hover {
				svg {
					transform: scale(1.4);
				}
			}
		}
	}
	&--active {
		height: 100%;
		z-index: 4;
		transition: 0.8s height, 0.3s z-index;
		@media (max-width: 680px) {
			height: auto !important;
			max-height: none !important;
		}
		.item-goods__content-grass {
			opacity: 1;
			visibility: visible;
		}
		.item-goods__body {
			opacity: 1;
			visibility: visible;
			position: relative;
			z-index: 10;
			transition: 0.5s opacity 0.3s, 0.5s visibility 0.3s, 0.8s transform 0.3s;
			transform: translateY(0px);
		}
		.item-goods__content--green {
			&:after {
				opacity: 1;
				visibility: visible;
				transition: 0.8s opacity, 0.8s visibility;
			}
			.item-goods__title {
				span {
					color: #73be42;
				}
				svg {
					transform: rotate(180deg);
				}
				path {
					fill: #73be42;
				}
			}
		}
		.item-goods__content--violet {
			&:after {
				opacity: 1;
				visibility: visible;
				transition: 0.8s opacity, 0.8s visibility;
			}
			.item-goods__title {
				span {
					color: #784c9f;
				}
				svg {
					transform: rotate(180deg);
				}
				path {
					fill: #784c9f;
				}
			}
		}
		.item-goods__content--blue {
			&:after {
				opacity: 1;
				visibility: visible;
				transition: 0.8s opacity, 0.8s visibility;
			}
			.item-goods__title {
				span {
					color: #18ade3;
				}
				svg {
					transform: rotate(180deg);
				}
				path {
					fill: #18ade3;
				}
			}
		}
		.item-goods__content--pink {
			&:after {
				opacity: 1;
				visibility: visible;
				transition: 0.8s opacity, 0.8s visibility;
			}
			.item-goods__title {
				span {
					color: #bb066e;
				}
				svg {
					transform: rotate(180deg);
				}
				path {
					fill: #bb066e;
				}
			}
		}
	}
}

.goods-item {
	// overflow-y: auto;
	margin-bottom: 1rem;
	@media (max-width: 1024px) {
		overflow-y: auto;
		// height: 700px;
	}
	@media (max-width: 680px) {
		height: auto;
		margin-bottom: 3.5rem;
	}
	@media (max-width: 480px) {
		height: auto;
		margin-bottom: 1.5rem;
	}
	&__row {
		display: flex;
		margin-bottom: rem(30px);
		@media (max-width: 1024px) {
			flex-wrap: wrap;
		}
	}
	&__column {
		width: 50%;
		@media (max-width: 1024px) {
			width: 100%;
			margin-bottom: 18px;
			order: 2;
		}
		&--image {
			display: flex;
			justify-content: flex-end;
			order: 1;
			@media (max-width: 1024px) {
				justify-content: center;
				order: 1;
			}
		}
	}
	&__info {
		font-size: rem(24px);
		line-height: rem(29px);
		margin-bottom: rem(30px);
		padding: rem(0px) rem(40px) 0 0;
		&--green {
			color: #73be42;
		}
		&--violet {
			color: #784c9f;
		}
		&--blue {
			color: #18ade3;
		}
		&--pink {
			color: #bb066e;
		}
		@media (max-width: 1180px) {
			font-size: rem(20px);
			line-height: rem(24px);
			margin-bottom: rem(18px);
		}
	}
	&__buy-button {
		position: relative;
		align-items: center;
		justify-content: center;
		background: #e8f0f5;
		box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2), inset 0px -4px 4px #b5c0c7, inset 0px 4px 4px #ffffff;
		border-radius: 36px;
		min-height: 39px;
		display: none;
		transition: 0.3s background;
		span {
			font-weight: bold;
			font-size: 16px;
			line-height: 19px;
		}

		&:after {
			content: "";
			background: url("data:image/svg+xml;base64,")
				0 0 no-repeat;
			width: 16px;
			height: 10px;
			position: absolute;
			transition: 0.3s transform;
			top: 15px;
			right: 13px;
		}
		@media (max-width: 1024px) {
			&:active {
				background: #fafcfe;
				box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2), inset 0px -4px 4px #b5c0c7, inset 0px 4px 4px #ffffff;
			}
		}
		@media (max-width: 680px) {
			display: flex;
		}
	}
	&__text-button {
		position: relative;
		align-items: center;
		justify-content: center;
		background: #e8f0f5;
		box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2), inset 0px -4px 4px #b5c0c7, inset 0px 4px 4px #ffffff;
		border-radius: 36px;
		min-height: 39px;
		display: none;
		max-width: 205px;
		width: 100%;
		margin: 0 auto;
		transition: 0.3s background;
		span {
			font-weight: bold;
			font-size: 16px;
			line-height: 19px;
			&:nth-child(2) {
				display: none;
			}
		}
		&:after {
			content: "";
			background: url("data:image/svg+xml;base64,")
				0 0 no-repeat;
			width: 16px;
			height: 10px;
			position: absolute;
			transition: 0.3s transform;
			top: 15px;
			right: 13px;
		}
		@media (max-width: 1024px) {
			&:active {
				background: #fafcfe;
				box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2), inset 0px -4px 4px #b5c0c7, inset 0px 4px 4px #ffffff;
			}
		}
		@media (max-width: 680px) {
			display: flex;
		}
	}
	&__labels {
		&-item {
			display: flex;
			align-items: center;
			justify-content: center;
			width: 100%;
			height: rem(48px);
			max-width: rem(180px);
			margin-bottom: rem(8px);
			background: #e8f0f5;
			box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2), inset 0px -4px 4px #b5c0c7, inset 0px 4px 4px #ffffff;
			border-radius: rem(30px);
			transition: 0.3s background, 0.3s box-shadow;
			&:last-child {
				margin-bottom: 0;
			}
			@media (min-width: 1024px) {
				&:hover {
					background: #ffffff;
					box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2), inset 0px -4px 4px #b5c0c7, inset 0px 4px 4px #ffffff;
				}
			}
			@media (max-width: 1024px) {
				&:active {
					background: #ffffff;
					box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2), inset 0px -4px 4px #b5c0c7, inset 0px 4px 4px #ffffff;
				}
			}
			@media (max-width: 1024px) {
				max-width: rem(280px);
			}
			@media (max-width: 480px) {
				max-width: rem(207px);
			}
		}
		&-logo {
			width: rem(113px);
			height: rem(26px);
			position: relative;
			img {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				object-fit: contain;
			}
		}
		@media (max-width: 1024px) {
			flex-direction: column;
			display: flex;
			align-items: center;
		}
		@media (max-width: 680px) {
			display: none;
		}
	}
	&__image {
		width: rem(222px);
		height: rem(320px);
		position: relative;
		@media (max-width: 1180px) {
			width: rem(182px);
			height: rem(280px);
		}
		@media (max-width: 1080px) {
			width: rem(162px);
			height: rem(260px);
		}
		img {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			object-fit: contain;
			z-index: 1;
		}
		&-label {
			position: absolute;
			bottom: rem(-20px);
			left: rem(-35px);
			z-index: 2;
			width: rem(97px);
			height: rem(97px);
			svg {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				object-fit: contain;
			}
		}
	}
	&__text {
		@media (max-width: 840px) {
			height: auto;
			display: flex;
			flex-direction: column;
		}
		@media (max-width: 680px) {
			padding: 0 0 20px 0;
		}
		p {
			margin-bottom: rem(24px);
			font-weight: 400;
			font-size: rem(20px);
			line-height: rem(24px);
			color: #ffffff;
			span {
				font-weight: bold;
				margin-right: rem(8px);
			}
			&:last-child {
				margin-bottom: 0px;
			}
			@media (max-width: 1400px) {
				font-size: rem(19px);
				line-height: rem(22px);
				margin-bottom: rem(22px);
			}

			@media (max-width: 1320px) {
				font-size: rem(18px);
				line-height: rem(20px);
				margin-bottom: rem(18px);
			}
		}
	}
	&__list {
		li {
			position: relative;
			font-weight: 400;
			font-size: rem(19px);
			line-height: rem(23px);
			color: #ffffff;
			margin-bottom: rem(16px);
			padding: 0 0 0 rem(18px);
			&:last-child {
				margin-bottom: 0px;
			}
			&:before {
				position: absolute;
				top: rem(7px);
				left: 0;
				content: "";
				width: rem(10px);
				height: rem(10px);
				background: #fff;
				border-radius: 50%;
			}
			@media (max-width: 1400px) {
				font-size: rem(19px);
				line-height: rem(22px);
				margin-bottom: rem(15px);
			}
			@media (max-width: 1320px) {
				font-size: rem(18px);
				line-height: rem(20px);
				margin-bottom: rem(14px);
			}
		}
		@media (max-width: 680px) {
			display: none;
		}
	}
}

.mobile-button--active {
	margin-bottom: 40px;
	&:after {
		transform: rotate(180deg);
	}
	~ .goods-item__labels {
		display: flex;
	}
}

.mobile-text--active {
	margin-bottom: 40px;
	&:after {
		transform: rotate(180deg);
	}
	span {
		&:nth-child(1) {
			display: none;
		}
		&:nth-child(2) {
			display: inline-block;
		}
	}
	~ .goods-item__list {
		display: block !important;
	}
}
