.buy {
	padding: rem(430px) rem(80px) rem(185px);
	position: relative;
	@media (max-width: 768px) {
		padding: rem(130px) rem(40px) rem(155px);
	}
	@media (max-width: 480px) {
		padding: rem(130px) rem(18px) rem(95px);
	}
	&__oblako-01 {
		position: absolute;
		min-width: rem(168px);
		left: 12%;
		top: 5%;
		z-index: 6;
		img {
			width: 100%;
		}
		@media (max-width: 1024px) {
			display: none;
		}
	}
	&__oblako-02 {
		position: absolute;
		min-width: rem(168px);
		right: 2%;
		top: 1%;
		z-index: 6;
		img {
			width: 100%;
		}
		@media (max-width: 1024px) {
			display: none;
		}
	}
	&__grass {
		position: absolute;
		bottom: -26%;
		left: 50%;
		transform: translateX(-50%);
		width: 100%;
		max-height: rem(840px);
		min-width: rem(1440px);
		z-index: 8;
		background: linear-gradient(
			180deg,
			rgba(255, 255, 255, 0) 0%,
			rgba(255, 255, 255, 1) 32%,
			rgba(255, 255, 255, 1) 100%
		);
		img {
			width: 100%;
		}
		@media (max-width: 1240px) {
			bottom: -5%;
		}
		@media (max-width: 768px) {
			min-width: rem(1068px);
			bottom: 10%;
		}
		@media (max-width: 480px) {
			min-width: rem(768px);
			bottom: 25%;
		}
	}
	&__tree-01 {
		position: absolute;
		width: rem(249px);
		z-index: 7;
		bottom: 47%;
		left: 26%;
		img {
			width: 100%;
		}
		@media (max-width: 1240px) {
			left: 20%;
			bottom: 57%;
		}
		@media (max-width: 1024px) {
			left: 10%;
		}
		@media (max-width: 768px) {
			display: none;
		}
	}
	&__tree-02 {
		position: absolute;
		width: rem(138px);
		z-index: 7;
		right: 30%;
		bottom: 50%;
		img {
			width: 100%;
		}
		@media (max-width: 1240px) {
			right: 25%;
			bottom: 60%;
		}
		@media (max-width: 1024px) {
			right: 15%;
		}
		@media (max-width: 768px) {
			display: none;
		}
	}
	&__title {
		position: relative;
		z-index: 50;
		font-weight: bold;
		font-size: rem(60px);
		line-height: rem(72px);
		color: #fff;
		text-align: center;
		margin-bottom: rem(41px);
		opacity: 0;
		transform: translateY(-80%);
		transition: 1s opacity 0.3s, 1s transform 0.3s;
		@media (max-width: 768px) {
			font-size: rem(32px);
			line-height: 46px;
		}
		@media (max-width: 480px) {
			font-size: rem(22px);
			line-height: 26px;
		}
		&._active {
			opacity: 1;
			transform: translateY(0%);
		}
	}
	&__text {
		opacity: 0;
		transform: translateY(-80%);
		transition: 1s opacity 0.3s, 1s transform 0.3s;
		position: relative;
		z-index: 50;
		max-width: rem(561px);
		margin: 0 auto;
		font-size: rem(24px);
		line-height: rem(29px);
		color: #ffffff;
		margin-bottom: 66px;
		font-weight: normal;
		@media (max-width: 768px) {
			font-size: 20px;
			line-height: 24px;
		}
		@media (max-width: 480px) {
			font-size: 16px;
			line-height: 19px;
		}
		&._active {
			opacity: 1;
			transform: translateY(0%);
		}
	}
	&__row {
		opacity: 0;
		transform: translateY(-80%);
		transition: 1s opacity 0.3s, 1s transform 0.3s;
		position: relative;
		z-index: 50;
		display: flex;
		align-items: flex-start;
		justify-content: center;

		@media (max-width: 1280px) {
			flex-direction: column;
			align-items: center;
		}
		@media (max-width: 480px) {
			margin-bottom: 30px;
		}
		&._active {
			opacity: 1;
			transform: translateY(0%);
		}
	}
	&__button {
		cursor: pointer;
		width: 100%;
		height: rem(73px);
		padding: rem(18px) rem(63px);
		display: flex;
		align-items: center;
		justify-content: center;
		&:after {
			position: absolute;
			content: "";
			right: rem(16px);
			top: rem(32px);
			width: rem(20px);
			height: rem(12px);
			background: url("data:image/svg+xml;base64,")
				0 0 no-repeat;
			transition: 0.3s transform;
		}
	}
	&__container {
		position: relative;
		z-index: 50;
		top: 100px;
	}
	&__item {
		position: relative;
		margin-right: rem(31px);
		max-width: rem(299px);
		width: 100%;
		height: rem(73px);
		z-index: -1;
		transition: 0.5s z-index;
		&:last-child {
			margin: 0px 0px 0px 0px;
		}
		&--active {
			z-index: 5;
			transition: 0.5s z-index;
			.buy__body {
				position: static;
				opacity: 1;
				visibility: visible;
			}
			.buy__button {
				&:after {
					transform: rotate(180deg);
					transition: 0.3s transform;
				}
			}
		}
		@media (max-width: 1240px) {
			margin-right: 0;
			margin-bottom: 8px;
			&:last-child {
				margin: 0px 0px 0px 0px;
			}
		}
	}
	&__apteka {
		background: #c0e2ab;
		box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2), inset 0px -8px 8px #a9ca95, inset 0px 8px 8px #d8fbc2;
		position: absolute;
		top: 0;
		left: 0;
		border-radius: rem(30px);
		transition: 0.3s background, 0.3s box-shadow;
		max-width: 100%;
		@media (min-width: 1024px) {
			&:hover {
				transition: 0.3s background, 0.3s box-shadow;
				background: #e0f1d5;
				box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2), inset 0px -8px 8px #bfddac, inset 0px 8px 8px #fcfffa;
			}
		}
	}
	&__logo {
		width: rem(176px);
		height: rem(46px);
		position: relative;
		img {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			object-fit: contain;
		}
	}
	&__body {
		padding: rem(18px) rem(63px) rem(28px);
		opacity: 0;
		visibility: hidden;
		display: none;
	}
	&__list {
		li {
			margin-bottom: rem(24px);
			&:last-child {
				margin-bottom: 0;
			}
		}
	}
	&__link {
		font-weight: bold;
		font-size: rem(16px);
		line-height: rem(19px);
	}
}

sup {
	font-size: 0.7em;
}
