.footer {
	position: relative;
	z-index: 50;
	&__bear {
		position: absolute;
		right: rem(-22px);
		top: rem(-20px);
		width: rem(195px);
		height: rem(178px);
		opacity: 0;
		transform: translateY(190px);
		transition: 1s opacity 0.8s, 1s transform 0.8s;
		img {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			object-fit: contain;
		}
		@media (max-width: 399px) {
			top: -10px;
			width: 160px;
			height: 143px;
			transform: rotate(7deg);
		}
		&._active {
			opacity: 1;
			transform: translateY(0%);
		}
	}
	&__body {
		position: relative;
		z-index: 3;
		height: rem(282px);
		background: linear-gradient(155.64deg, #8cbb06 28.84%, #ffffff 115.01%), #73be42;
		border-radius: 0px rem(250px) 0 0;
		display: flex;
		align-items: center;
		@media (max-width: 479.98px) {
			align-items: flex-end;
		}
		@media (max-width: 399px) {
			border-radius: 0px 225px 0 0;
		}
	}
	&__row {
		display: flex;
		align-items: center;
		width: 100%;
		opacity: 0;
		transform: translateY(-30px);
		transition: 1s opacity 0.3s, 1s transform 0.3s;
		@media (max-width: 649px) {
			flex-wrap: wrap;
		}
		@media (max-width: 479.98px) {
			padding: 0px 0px 40px 0px;
		}
		&._active {
			opacity: 1;
			transform: translateY(0%);
		}
	}
	&__column {
		width: 50%;
		display: flex;
		align-items: center;
		&--right {
			padding: 0 0 0 rem(130px);
			@media (max-width: 767.98px) {
				width: 65%;
				padding: 0px 50px 0 50px;
			}
			@media (max-width: 649px) {
				padding: 25px 0 0 0;
			}
		}
		@media (max-width: 649px) {
			width: 100%;
		}
	}
	&__logo {
		flex: 0 0 rem(197px);
		width: rem(197px);
		height: rem(57px);
		position: relative;
		margin-right: rem(130px);
		@media (max-width: 1440px) {
			margin: 0px 130px / 1440px * 100vw 0px 0px;
		}
		@media (max-width: 1024.98px) {
			margin: 0px 20px 0px 0px;
		}
		@media (max-width: 767.98px) {
			flex: 0 0 150px;
			width: 150px;
		}
		@media (max-width: 479.98px) {
			display: none;
		}
		img {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			object-fit: contain;
		}
	}
	&__socials {
		display: flex;
		align-items: center;
	}
	&__links {
		display: inline-flex;
		align-items: center;
		justify-content: center;
		width: rem(30px);
		height: rem(30px);
		border-radius: 50%;
		border: 1px solid #fff;
		margin-right: rem(14px);
		&:last-child {
			margin-right: 0px;
		}
		span {
			width: rem(14.5px);
			height: rem(16.5px);
			position: relative;
			display: inline-block;
			svg {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				object-fit: contain;
			}
		}
	}
	&__creator {
		&-logo {
			position: relative;
			width: rem(85px);
			height: rem(38px);
			margin-right: rem(16px);
			img {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				object-fit: contain;
			}
		}
		&-text {
			font-size: rem(12px);
			line-height: rem(14px);
			color: #219653;
		}
	}
}
